.footer {
  margin: 120px 0 0 40px;
}

.fotter__menu {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 24px;
}

.footer__menu-items {
  display: flex;
  flex-direction: column;
  gap: 16px;
  width: 322px;
}

.footer__menu-title {
  margin-bottom: 8px;
  font-weight: 500;
  font-size: 24px;
}

.footer__menu-link {
  font-weight: 500;
  font-size: 20px;
  color: #868e96;
}

.footer__contacts {
  display: flex;
  justify-content: flex-start;
  margin: 64px 0 40px;
}

.footer__contacts-soc {
  display: flex;
  justify-content: flex-start;
  margin-right: 20px;
}

.footer__contacts-mail {
  font-weight: 500;
  font-size: 24px;
  color: #868e96;
  margin-top: 16px;
}
